import {useEffect, lazy, Suspense, useState} from 'react';
import { Route, Switch, useRouteMatch, useHistory, Redirect, Link, useLocation, useParams } from 'react-router-dom';

import L from 'leaflet';
import { MapContainer, useMap, useMapEvent, TileLayer, Marker, Popup } from 'react-leaflet';
import LocationIcon from '../icons/LocationIcon'
import logo from "../pages/image/logo.svg"
import logos from "../pages/image/lhome.png"
import {home} from 'react-icons-kit/fa/home'
import {useDispatch, useSelector} from 'react-redux';
import { post, fetchData } from '../application/middlewares/index';
import { Icon } from 'react-icons-kit'
// import {home} from 'react-icons-kit/fa/home';



const Navbar = lazy(() => import('./Navbar'))
const Footer = lazy(() => import('./Footer'))


function AllMap(){
    const dispatch = useDispatch();

    const myIcon = L.icon({
        iconUrl: 'https://gamysh.com/uploads/images/lhome.png',
        iconSize: [40,40],
        iconAnchor: [32, 64],
        popupAnchor: null,
        shadowUrl: null,
        shadowSize: null,
        shadowAnchor: null
    });
    const [locations, setLocations] = useState();

    useEffect(()=>{
        dispatch(fetchData({
            url:`tm/get-real-estate-positions`,
            token:'',
            action:(response)=>{
                if(response.success){
                    console.log("get_positions ",response)
                    // console.log(response.data.rows);
                    setLocations(response.data.rows);
                } else {
                    console.log("error_data ",response);
                    }
                }
            }))
    },[])
    console.log(locations);
       
    return(
        <main>
            <Navbar />
            <hr />
            <div className="w-full my-5 bg-red-100">
            <MapContainer 
                center={[37.92227, 58.37601]} 
                zoom={12} scrollWheelZoom={false}
                style={{height:500}}
                >
                    
                    {/* <LocationIcon className="absolute text-gray-800 top-1/2 left-1/2 text-5xl -mt-11 -ml-6" style={{zIndex:100000}}/> */}

                    <TileLayer

                        url="https://gamysh.com/map/tile/{z}/{x}/{y}.png"
                        attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                    />
                    {locations?.map((position)=>(
                        <Marker 
                        position={[position?.lat, position?.lng]}  
                        icon={ 
                            // <Icon size={25} icon={home} className="text-blue-600" />
                            myIcon
                         }
                        >
                        </Marker>
                    ))}
               
            </MapContainer>
            </div>
            <Footer />
        </main>

    )
}

export default AllMap;